import Vue from 'vue';
import { v4 as uuidv4 } from 'uuid';
var state = {
    socket: {
        isConnected: false,
        reconnectError: false,
    },
    requests: {},
    responses: {},
};
var actions = {
    sendRequest: function (context, request) {
        if (request.data.msgId === undefined) {
            request.data.msgId = uuidv4();
        }
        Vue.prototype.$socket.sendObj(request);
        context.commit('SOCKET_ONSEND', request);
    },
};
var getters = {
    isSocketConnected: function (state) {
        return state.socket.isConnected;
    },
};
var mutations = {
    SOCKET_ONOPEN: function (state, event) {
        Vue.prototype.$socket = event.currentTarget;
        state.socket.isConnected = true;
    },
    // eslint-disable-next-line no-unused-vars
    SOCKET_ONCLOSE: function (state) {
        state.socket.isConnected = false;
    },
    SOCKET_ONERROR: function (state, event) {
        console.error(state, event);
    },
    SOCKET_ONMESSAGE: function (state, message) {
        state.responses[message.data.msgId] = message;
    },
    SOCKET_ONSEND: function (state, request) {
        state.requests[request.data.msgId] = request;
    },
    SOCKET_RECONNECT: function (state, count) {
        // eslint-disable-next-line no-console
        console.info(state, count);
    },
    SOCKET_RECONNECT_ERROR: function (state) {
        state.socket.reconnectError = true;
    },
};
export default {
    state: state,
    actions: actions,
    getters: getters,
    mutations: mutations,
};
